import React from 'react';
import './App.css';
import Home from './home';
import { Route, Link } from 'react-router-dom'
import Aadhar from './aadhar';
import Otp from './otp';
import Pan from './pan';
import Digilocker from './digilocker';
import Manual from './manual';
import Complete from './complete';
import bgform from './images/try2.svg'
import Circle from './components/circle/circle';
function App() {
  return (
    <div className="App " style={{
      backgroundImage: 'url('+bgform+')',
      backgroundSize: "cover",
      top: "0px",
      height: "100vh",
      width: "100vw",
      // overflow:"scroll"
      // color: "#f5f5f5"
    }}>
      <Route exact to="/" component={Home}></Route>
      <Route exact path="/invoidEkyc/aadhaar" component={Aadhar}></Route>
      <Route exact path="/invoidEkyc/Otp" component={Otp}></Route>
      <Route exact path="/invoidEkyc/Pan" component={Pan}></Route>
      <Route exact path="/invoidEkyc/digilocker" component={Digilocker}></Route>
      <Route exact path="/invoidEkyc/manual" component={Manual}></Route>
      <Route exact path="/invoidEkyc/complete" component={Complete}></Route>
      <Link Classname="button" to="/"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="/invoidEkyc/aadhaar"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="/invoidEkyc/Pan"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="/invoidEkyc/digilocker"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="/invoidEkyc/Otp"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="./manual"><Circle className="next"></Circle></Link>
      <Link Classname="button" to="/invoidEkyc/complete"><Circle className="next"></Circle></Link>
    </div>
  );
}

export default App;
